html {
  height: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: "Nunito", sans-serif;
  background: #E8E8E8;
  font-size: 20px;
  color: #333333;
  line-height: 1.35;
  overflow-x: hidden;
  font-optical-sizing: auto;
}
body.m-hidden {
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

:-moz-any-link:focus {
  outline: 1px dotted invert;
}

:-moz-any-link:focus {
  outline: none;
}

textarea,
input {
  box-shadow: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

::-webkit-input-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

::-moz-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

:-moz-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

:-ms-input-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

:focus::-moz-placeholder {
  color: transparent;
}

:focus:-moz-placeholder {
  color: transparent;
}

:focus:-ms-input-placeholder {
  color: transparent;
}

input:required {
  -webkit-box-shadow: none;
  box-shadow: none;
}

input[type=text]:-webkit-autofill {
  font-size: 20px !important;
}

input[type=text]:-webkit-autofill,
input[type=text]:-webkit-autofill:hover,
input[type=text]:-webkit-autofill:focus,
input[type=text]:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  font-size: 20px !important;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  box-shadow: 0 0 0px 1000px #fff inset;
}

input:-webkit-autofill::first-line {
  font-size: 20px !important;
}

input[type=submit] {
  border: 0;
}

button {
  border: 0;
  outline: none;
}

.layout a:not([class]),
.page-popup a:not([class]) {
  color: var(--main-color);
  text-decoration: none;
}
.layout h1,
.page-popup h1 {
  font-size: 94px;
  font-family: "Unbounded", sans-serif;
  margin: 0 0 43px 0;
  line-height: 1.2;
  font-weight: 400;
}
.layout h2,
.page-popup h2 {
  font-size: 50px;
  margin: 0 0 38px 0;
  line-height: 1.2;
  font-family: "Unbounded", sans-serif;
  font-optical-sizing: auto;
  font-weight: normal;
  font-style: normal;
}
.layout h3,
.page-popup h3 {
  font-size: 40px;
  margin: 0 0 24px 0;
  font-family: "Unbounded", sans-serif;
  font-optical-sizing: auto;
  font-weight: normal;
  font-style: normal;
}
.layout h4,
.page-popup h4 {
  font-size: 24px;
  margin: 0 0 20px 0;
  font-family: "Unbounded", sans-serif;
  font-optical-sizing: auto;
  font-weight: normal;
  font-style: normal;
}
.layout ul,
.page-popup ul {
  margin: 0;
  padding: 0;
}
.layout ul li,
.page-popup ul li {
  display: flex;
  align-items: flex-start;
  list-style-type: none;
  margin-bottom: 16px;
}
.layout ul li:last-child,
.page-popup ul li:last-child {
  margin-bottom: 0;
}
.layout ul li:before,
.page-popup ul li:before {
  content: "";
  width: 6px;
  min-width: 6px;
  height: 6px;
  background: #000;
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
  margin: 11px 12px 0 5px;
}
.layout ol,
.page-popup ol {
  margin: 0 0 20px 0;
  padding: 0;
  counter-reset: a;
}
.layout ol:last-child,
.page-popup ol:last-child {
  margin-bottom: 0;
}
.layout ol li,
.page-popup ol li {
  margin: 0 0 20px 0;
  padding-left: 30px;
  position: relative;
  list-style-type: none;
}
.layout ol li:last-child,
.page-popup ol li:last-child {
  margin-bottom: 0;
}
.layout ol li:before,
.page-popup ol li:before {
  counter-increment: a;
  content: counter(a);
  position: absolute;
  left: 0;
  top: 2px;
  color: var(--main-color);
}
.layout input[type=checkbox]:not(:checked),
.layout input[type=checkbox]:checked,
.page-popup input[type=checkbox]:not(:checked),
.page-popup input[type=checkbox]:checked {
  position: absolute;
  left: -9999px;
}
.layout input[type=checkbox]:not(:checked) + label,
.layout input[type=checkbox]:checked + label,
.page-popup input[type=checkbox]:not(:checked) + label,
.page-popup input[type=checkbox]:checked + label {
  position: relative;
  cursor: pointer;
  padding-left: 32px;
  line-height: 23px;
  font-size: 16px;
  display: inline-block;
}
.layout input[type=checkbox]:not(:checked) + label a,
.layout input[type=checkbox]:checked + label a,
.page-popup input[type=checkbox]:not(:checked) + label a,
.page-popup input[type=checkbox]:checked + label a {
  color: #333;
  text-decoration: underline;
}
.layout input[type=checkbox]:not(:checked) + label a:hover,
.layout input[type=checkbox]:checked + label a:hover,
.page-popup input[type=checkbox]:not(:checked) + label a:hover,
.page-popup input[type=checkbox]:checked + label a:hover {
  text-decoration: none;
}
.layout input[type=checkbox]:not(:checked) + label:before,
.page-popup input[type=checkbox]:not(:checked) + label:before {
  background: #E6E6E6;
}
.layout input[type=checkbox]:checked + label:before,
.page-popup input[type=checkbox]:checked + label:before {
  background: #fff;
}
.layout input[type=checkbox]:not(:checked) + label:before,
.layout input[type=checkbox]:checked + label:before,
.page-popup input[type=checkbox]:not(:checked) + label:before,
.page-popup input[type=checkbox]:checked + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 23px;
  height: 23px;
  border-radius: 5px;
  box-shadow: inset 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
  z-index: 5;
}
.layout input[type=checkbox]:not(:checked) + label:after,
.layout input[type=checkbox]:checked + label:after,
.page-popup input[type=checkbox]:not(:checked) + label:after,
.page-popup input[type=checkbox]:checked + label:after {
  content: "";
  background: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.91304L4.0303 7.92308L11 1' stroke='%2341DAA4' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E") center no-repeat;
  width: 12px;
  height: 10px;
  display: inline-block;
  position: absolute;
  top: 6px;
  left: 6px;
  z-index: 10;
}
.layout input[type=checkbox]:not(:checked) + label:after,
.page-popup input[type=checkbox]:not(:checked) + label:after {
  opacity: 0;
}
.layout input[type=checkbox]:checked + label:after,
.page-popup input[type=checkbox]:checked + label:after {
  opacity: 1;
}

:root {
  --iti-hover-color: rgba(0, 0, 0, 0.05);
  --iti-border-color: #ccc;
  --iti-dialcode-color: #999;
  --iti-dropdown-bg: white;
  --iti-spacer-horizontal: 15px;
  --iti-flag-height: 21px;
  --iti-flag-width:28px;
  --iti-border-width: 1px;
  --iti-arrow-height: 4px;
  --iti-arrow-width: 6px;
  --iti-triangle-border: calc(var(--iti-arrow-width) / 2);
  --iti-arrow-padding: 11px;
  --iti-arrow-color: #000;
  --iti-path-flags-1x: url("../img/content/flags.webp");
  --iti-path-flags-2x: url("../img/content/flags@2x.webp");
  --iti-path-globe-1x: url("../img/content/globe.webp");
  --iti-path-globe-2x: url("../img/content/globe@2x.webp");
  --iti-flag-sprite-width: 6832px;
  --iti-flag-sprite-height: 21px;
  --iti-mobile-popup-margin: 30px;
}

.iti {
  position: relative;
  display: inline-block;
}

.iti * {
  box-sizing: border-box;
}

.iti__hide {
  display: none;
}

.iti__v-hide {
  visibility: hidden;
}

.iti__a11y-text {
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  position: absolute;
}

.iti input.iti__tel-input,
.iti input.iti__tel-input[type=text],
.iti input.iti__tel-input[type=tel] {
  position: relative;
  z-index: 0;
  margin: 0 !important;
}

.iti__country-container {
  position: absolute;
  top: 0;
  bottom: 0;
  padding: var(--iti-border-width);
}

.iti__selected-country {
  z-index: 1;
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  border-radius: 0;
  font-weight: inherit;
  line-height: inherit;
  text-decoration: none;
}

.iti__selected-country-primary {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 var(--iti-arrow-padding) 0 var(--iti-spacer-horizontal);
}

.iti__arrow {
  margin-left: var(--iti-arrow-padding);
  width: 0;
  height: 0;
  border-left: var(--iti-triangle-border) solid transparent;
  border-right: var(--iti-triangle-border) solid transparent;
  border-top: var(--iti-arrow-height) solid var(--iti-arrow-color);
}

[dir=rtl] .iti__arrow {
  margin-right: var(--iti-arrow-padding);
  margin-left: 0;
}

.iti__arrow--up {
  border-top: none;
  border-bottom: var(--iti-arrow-height) solid var(--iti-arrow-color);
}

.iti__dropdown-content {
  border-radius: 3px;
  background-color: var(--iti-dropdown-bg);
}

.iti--inline-dropdown .iti__dropdown-content {
  position: absolute;
  z-index: 2;
  margin-top: 3px;
  margin-left: calc(var(--iti-border-width) * -1);
  border: var(--iti-border-width) solid var(--iti-border-color);
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
}

.iti__search-input {
  width: 100%;
  border-width: 0;
  border-radius: 3px;
}

.iti__search-input + .iti__country-list {
  border-top: 1px solid var(--iti-border-color);
}

.iti__country-list {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.iti--inline-dropdown .iti__country-list {
  max-height: 185px;
}

.iti--flexible-dropdown-width .iti__country-list {
  white-space: nowrap;
}

@media (max-width: 500px) {
  .iti--flexible-dropdown-width .iti__country-list {
    white-space: normal;
  }
}
.iti__country {
  display: flex;
  align-items: center;
  padding: 8px var(--iti-spacer-horizontal);
  outline: none;
}

.iti__dial-code {
  color: var(--iti-dialcode-color);
}

.iti__country.iti__highlight {
  background-color: var(--iti-hover-color);
}

.iti__country-list .iti__flag, .iti__country-name {
  margin-right: var(--iti-spacer-horizontal);
}

[dir=rtl] .iti__country-list .iti__flag, [dir=rtl] .iti__country-name {
  margin-right: 0;
  margin-left: var(--iti-spacer-horizontal);
}

.iti--allow-dropdown .iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly])):hover, .iti--allow-dropdown .iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly])):hover button {
  cursor: pointer;
}

.iti--allow-dropdown .iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly])) .iti__selected-country-primary:hover,
.iti--allow-dropdown .iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly])) .iti__selected-country:has(+ .iti__dropdown-content:hover) .iti__selected-country-primary {
  background-color: var(--iti-hover-color);
}

[dir=rtl] .iti .iti__selected-dial-code {
  margin-left: 0;
  margin-right: 4px;
}

.iti--container {
  position: fixed;
  top: -1000px;
  left: -1000px;
  z-index: 1060;
  padding: var(--iti-border-width);
}

.iti--container:hover {
  cursor: pointer;
}

.iti--fullscreen-popup.iti--container {
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  padding: var(--iti-mobile-popup-margin);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.iti--fullscreen-popup .iti__dropdown-content {
  display: flex;
  flex-direction: column;
  max-height: 100%;
  position: relative;
}

.iti--fullscreen-popup .iti__country {
  padding: 10px 10px;
  line-height: 1.5em;
}

.iti__flag {
  --iti-flag-offset: 100px;
  height: var(--iti-flag-height);
  width: var(--iti-flag-width);
  border-radius: 1px;
  box-shadow: 0px 0px 1px 0px #888;
